import type { GetStaticProps } from 'next';
import Link from 'next/link';
import { getSortedPostsData } from '../lib/posts';
import Nav from '../components/Nav';
import { useState } from 'react';
import styles from '../styles/Home.module.css';
import Footer from '../components/footer';
import { siteName } from '../config/siteConfig';

export default function Home({ allPostsData }: { allPostsData: { id: string; title: string; date: string | Date; summary?: string; tags?: string[] }[] }) {
  const [currentPage, setCurrentPage] = useState(1);
  const postsPerPage = 10;
  
  const indexOfLastPost = currentPage * postsPerPage;
  const indexOfFirstPost = indexOfLastPost - postsPerPage;
  const currentPosts = allPostsData.slice(indexOfFirstPost, indexOfLastPost);
  const totalPages = Math.ceil(allPostsData.length / postsPerPage);

  return (
    <div className={styles.container}>
      <Nav onHomeClick={() => setCurrentPage(1)} />
      <main className={styles.main}>
        
        
        <div className={styles.posts}>
          {currentPosts.map((post) => (
            <article key={post.id} className={styles.postCard}>
              <div className={styles.postHeader}>
                <Link href={`/posts/${post.id}`} onClick={() => setCurrentPage(1)}>
                  <h2 className={styles.postTitle}>{post.title}</h2>
                </Link>
                <time className={styles.postDate}>
                  {new Date(post.date).toISOString().slice(0, 10)}
                </time>
              </div>
              {post.tags && (
                <div className={styles.postTags}>
                  {post.tags.map(tag => (
                    <span key={tag} className={styles.postTag}>{tag}</span>
                  ))}
                </div>
              )}
              {post.summary && <p className={styles.postSummary}>{post.summary}</p>}
            </article>
          ))}
        </div>

        {totalPages > 1 && (
          <div className={styles.pagination}>
            {currentPage > 2 && (
              <button
                key={1}
                onClick={() => setCurrentPage(1)}
                className={styles.pageButton}
              >
                1
              </button>
            )}
            {currentPage > 3 && <span className={styles.ellipsis}>...</span>}
            {Array.from(
              { 
                length: Math.min(3, totalPages) 
              }, 
              (_, i) => {
                let page = currentPage - 1 + i;
                if (currentPage < 2) page = i + 1;
                if (currentPage > totalPages - 2) page = totalPages - 2 + i;
                return page;
              }
            )
              .filter(page => page >= 1 && page <= totalPages)
              .map((number) => (
                <button
                  key={number}
                  onClick={() => setCurrentPage(number)}
                  className={`${styles.pageButton} ${
                    currentPage === number ? styles.active : ''
                  }`}
                >
                  {number}
                </button>
              ))}
            {currentPage < totalPages - 2 && <span className={styles.ellipsis}>...</span>}
            {currentPage < totalPages - 1 && (
              <button
                key={totalPages}
                onClick={() => setCurrentPage(totalPages)}
                className={styles.pageButton}
              >
                {totalPages}
              </button>
            )}
          </div>
        )}
      </main>

      <Footer siteName={siteName} />
    </div>
  );
}

export const getStaticProps: GetStaticProps = async () => {
  const allPostsData = getSortedPostsData().map(post => ({ ...post, summary: post.summary !== undefined ? post.summary : null, tags: post.tags !== undefined ? post.tags : null }));
  return {
    props: {
      allPostsData,
    },
  };
};
