import Link from 'next/link';
import styles from './Nav.module.css';
import { siteName } from '../config/siteConfig';
import { useRouter } from 'next/router';

interface NavProps {
  onHomeClick?: () => void;
}

export default function Nav({ onHomeClick }: NavProps) {
  const router = useRouter();
  
  return (
    <nav className={styles.nav}>
      <div className={styles.navContainer}>
        <Link href="/" className={styles.navLogo} onClick={onHomeClick}>
          {siteName}
        </Link>
        <div className={styles.navLinks}>
          <Link 
            href="/" 
            className={`${styles.navLink} ${router.pathname === '/' ? styles.active : ''}`}
            onClick={onHomeClick}
          >
            首页
          </Link>
          <Link 
            href="/about" 
            className={`${styles.navLink} ${router.pathname === '/about' ? styles.active : ''}`}
          >
            关于
          </Link>
        </div>
      </div>
    </nav>
  );
}